.weatherdetails {
    background-color: var(--color-primary);
    padding: 30px 0px 50px 0px;
    border-bottom-left-radius: 200px;
    border-bottom-right-radius: 200px;
    width: 100%;
    margin: auto;
    box-shadow: 0px 4px 4px rgba(0,0,0,.2);
}
.weatherdetails form{
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: auto;
}
.weatherdetails form input{
    height: 30px;
    border: none;
    border-bottom: 1px solid #000;
    background-color: transparent;
    margin-bottom: 20px;
    font-size: 18px;
}
.weatherdetails form input[type="text"]{
    color: white;
}
.weatherdetails form input::placeholder{
    color: white;
}
.weatherdetails form input:focus{
    outline: none;
    color: var(--color-light);
    background-color: transparent;
}
.weatherdetails form input:last-of-type{
    width: 30%;
    border: 1px solid var(--color-light);
    background-color: var(--color-light);
    color: var(--color-primary);
    border-radius: 10px;
    margin: auto;
    margin-top: 20px;
    padding-top: 3px;
    cursor: pointer;
    transition: 0.3s all ease-in-out;
}
.weatherdetails form input:last-of-type:hover{
  background-color: var(--color-primary);
  color: var(--color-light);
}
.weatherdetails h2{
    font-family: Arial, Helvetica, sans-serif;
    color: var(--color-light);
}
@media(max-width:700px){
    .weatherdetails form input:last-of-type{
        width: 70%;
    }
}