:root{
    --color-light:white;
    --color-primary:#36a0dd;
}
body{
    padding: 0px;
    margin: 0px;
    text-align: center;
}
.weathercontainer{
    width: 75%;
    margin: auto;
    border-bottom-left-radius: 200px;
    border-bottom-right-radius: 200px;
}
@media (max-width:700px) {
    .weathercontainer{
        border-bottom-left-radius: 100px;
        border-bottom-right-radius: 100px;
    }
}